import {
	debounce
} from '@/common/utils.js';
export default {
	data() {
		return {
			phone: '',
			version: uni.getStorageSync('version'),
			shade: false,
			navigationHeight:''
		};
	},
	onLoad() {

		let systemInfo = uni.getSystemInfoSync()
		// px转换到rpx的比例
		let pxToRpxScale = 750 / systemInfo.windowWidth;
		// 状态栏的高度
		let ktxStatusHeight = systemInfo.statusBarHeight * pxToRpxScale
		// 导航栏的高度
		let navigationHeight = 44 * pxToRpxScale
		this.navigationHeight=navigationHeight + 'rpx';
		console.log(ktxStatusHeight)
		console.log(navigationHeight)

		var p = uni.getStorageSync('phone');
		var p1 = p.split(''); //字符串转为数组
		var phone =
			p1
			.slice(0, 3)
			.join()
			.replace(/,/g, '') +
			'****' +
			p1
			.slice(7, 11)
			.join()
			.replace(/,/g, '');
		this.phone = phone;
	},
	onHide() {
		this.shade = false;
	},
	methods: {
		backfront: function() {
			uni.navigateBack({
				delta: 1
			})
		},
		logout: function() {
			this.shade = true;
		},
		moveHandle: function(e) {
			e.preventDefault();
			e.stopPropagation();
		},
		cancel: function() {
			this.shade = false;
		},
		sure: function() {
			this.global_.token == '';
			this.global_.phone == '';
			uni.removeStorageSync('phone');
			uni.removeStorageSync('token');
			uni.removeStorageSync('nowtime');
			uni.reLaunch({
				url:'../index/index'
			})
		},
		// 跳转路劲
		linkToTransfer: debounce(
			function() {
				uni.navigateTo({
					url: '../../my/personal/personal?phone=' + this.phone
				});
			},
			500,
			true
		),
		personal: function() {
			this.linkToTransfer();
		},
		linkToTransfer1: debounce(
			function() {
				uni.request({
					url: this.url + 'linkemails/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 200 || res.statusCode == 201) {
							uni.navigateTo({
								url: '../../my/email/email'
							});
						}
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/unbindemail/unbindemail'
							});
						}
					}
				});
			},
			500,
			true
		),
		mailaddress: function() {
			this.linkToTransfer1();
		},
		linkToTransfer2: debounce(
			function() {
				uni.request({
					url: this.url + 'realnames/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success: function(res) {
						if (res.statusCode == 202) {
							uni.showToast({
								title: '已实名认证',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 205) {
							uni.showToast({
								title: '身份认证审核中，请等待',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/identity/identity'
							});
						}
					}
				});
			},
			500,
			true
		),
		authentication: function() {
			this.linkToTransfer2();
		},
		linkToTransfer3: debounce(
			function() {
				uni.request({
					url: this.url + 'updataloginpwd/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/change-loginPassword/change-loginPassword'
							});
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/change-pass/change-pass'
							});
						}
					}
				});
			},
			500,
			true
		),
		loginpassword: function() {
			this.linkToTransfer3();
		},
		linkToTransfer4: debounce(
			function() {
				var that = this;
				uni.request({
					url: this.url + 'setmoneys/',
					method: 'GET',
					header: {
						Authorization: 'JWT' + ' ' + this.global_.token
					},
					success(res) {
						if (res.statusCode == 302) {
							uni.showToast({
								title: '用户未绑定邮箱',
								icon: 'none',
								duration: 2000
							});
							return false;
						}
						if (res.statusCode == 400) {
							uni.navigateTo({
								url: '../../my/change-password/change-password'
							});
						}
						if (res.statusCode == 200) {
							uni.navigateTo({
								url: '../../my/trade-password/trade-password'
							});
						}
					}
				});
			},
			500,
			true
		),
		radepassword: function() {
			this.linkToTransfer4();
		},
		// 返回
		onBack(){
			uni.navigateBack({
				delta:1
			})
		}
	}
};
